import React, { createContext, useEffect, useState } from "react";
import API from "../API/API";

export const TimerContext = createContext();

export const timerModeMap = {
  0: "timer",
  1: "countdown",
  2: "interval",
};

/*
  timerMode: client timer mode
  defaultCountdownTime: client's default countdown time
  defaultIntervalTime: client's defaut coutndown time
  countdownTime: client's set countdown
  intervalTime: client's set interval
    
*/

const TimerContextProvider = (props) => {
  const defaultTimes = {
    countdown: JSON.parse(localStorage.getItem("defaultCountdownTime")),
    interval: JSON.parse(localStorage.getItem("defaultIntervalTime")),
  };
  if (defaultTimes.countdown === null) {
    defaultTimes.countdown = [0, 1, 30];
  }

  if (defaultTimes.interval === null) {
    defaultTimes.interval = [0, 3, 0, 0, 0, 30, 0];
  }

  const [bleConnected, setBleConnected] = useState(false);
  const [bleObj, setBleObj] = useState({
    bleDevice: null,
    bleServer: null,
    bleService: null,
    timerControlCharacteristic: null,
    timerStateCharacteristic: null,
  });

  const [webBluetoothAvailable, setWebBluetoothAvailable] = useState(
    API.iOSCheck() && API.webBluetoothCheck()
  );
  const [bleFailed, setBleFailed] = useState(false);
  const [serverTimerDisplay, setServerTimerDisplay] = useState([
    0, 0, 0, 0, 0, 0,
  ]);
  const [serverTimerState, setServerTimerState] = useState([
    1, 0, 0, 30, 0, 0, 0, 0, 0,
  ]);
  const [timerMode, setTimerMode] = useState("current");
  const [defaultCountdownTime, setDefaultCountdownTime] = useState(
    defaultTimes.countdown
  );
  const [defaultIntervalTime, setDefaultIntervalTime] = useState(
    defaultTimes.interval
  );
  const [countdownTime, setCountdownTime] = useState(defaultTimes.countdown);
  const [intervalTime, setIntervalTime] = useState(defaultTimes.interval);
  const [settings, setSettings] = useState("");

  const handleSetDefaultCountdown = (defaultTime) => {
    const numbersArray = defaultTime.map((str) => parseInt(str, 10));
    setDefaultCountdownTime(numbersArray);
    localStorage.setItem("defaultCountdownTime", JSON.stringify(numbersArray));
  };

  const handleSetDefaultInterval = (defaultTime) => {
    if (defaultTime[6] === "∞") {
      defaultTime[6] = 0;
    }
    const numbersArray = defaultTime.map((str) => parseInt(str, 10));
    setDefaultIntervalTime(numbersArray);
    localStorage.setItem("defaultIntervalTime", JSON.stringify(numbersArray));
  };

  const handleBleDisconnect = (event) => {
    setBleConnected(false);
    bleObj.bleDevice = null;
    bleObj.bleServer = null;
    bleObj.bleService = null;
    bleObj.timerStateCharacteristic = null;
    bleObj.timerControlCharacteristic = null;
  };

  const handleStartStopTimer = async (value) => {
    const data = new Uint8Array([1, value]);
    await bleObj.timerControlCharacteristic.writeValue(data);
  };

  const handleTimerStateChange = (event) => {
    const text = new TextDecoder("utf-8").decode(event.target.value);
    const newValues = new Uint8Array(event.target.value.buffer);
    if (text.startsWith("!")) {
      setSettings(text.substring(1));
    } else if (text.startsWith("$")) {
      setSettings((prevSettings) => prevSettings + text.substring(1));
    } else if (newValues.length == 15) {
      setServerTimerDisplay(newValues.slice(0, 6));
      setServerTimerState(newValues.slice(6, 15));
    }
  };

  const handleSettingsUpdate = async (editedSettings) => {
    const n = 60;
    let i = 0;
    const encoder = new TextEncoder();

    let message = "!" + editedSettings.slice(i, i + n);
    await bleObj.timerControlCharacteristic.writeValue(encoder.encode(message));
    for (i = n; i < editedSettings.length - n; i += n) {
      message = "$" + editedSettings.slice(i, i + n);
      await bleObj.timerControlCharacteristic.writeValue(
        encoder.encode(message)
      );
    }
    i++;
    message = "?" + editedSettings.slice(i - 1, i + n);
    await bleObj.timerControlCharacteristic.writeValue(encoder.encode(message));
  };

  const handleSettingsReset = async () => {
    const data = new Uint8Array([4]);
    await bleObj.timerControlCharacteristic.writeValue(data);
  };

  const handleTimerControls = async (value) => {
    switch (value) {
      case 0:
        await handleStartStopTimer(0);
        break;
      case 1:
        await handleStartStopTimer(1);
        break;
      case 2:
        await handleStartStopTimer(2);
        break;
      case 3:
        await handleNewTimer();
        setTimerMode("current");
        break;
      default:
    }
  };

  const handleNewTimer = async () => {
    let data;
    if (timerMode == "timer") {
      data = new Uint8Array([2, 0, 0, 0, 0, 0, 0, 0, 0]);
    } else if (timerMode == "countdown") {
      data = new Uint8Array([2, 1, ...countdownTime, 0, 0, 0, 0]);
    } else {
      data = new Uint8Array([2, 2, ...intervalTime]);
    }
    await bleObj.timerControlCharacteristic.writeValue(data);
  };

  const requestTimerState = async (timerControlCharacteristic) => {
    try {
      const data = new Uint8Array([0]);
      await timerControlCharacteristic.writeValue(data);
    } catch (error) {
      console.error("request timerState failed", error);
    }
  };

  const requestSettings = async () => {
    try {
      const data = new Uint8Array([3]);
      await bleObj.timerControlCharacteristic.writeValue(data);
    } catch (error) {
      console.error("request timerState failed", error);
    }
  };

  const requestSettings2 = async (timerControlCharacteristic) => {
    try {
      const data = new Uint8Array([3]);
      await timerControlCharacteristic.writeValue(data);
    } catch (error) {
      console.error("request timerState failed", error);
    }
  };

  const connectBle = async () => {
    try {
      // throw new Error('Forced error for testing purposes')
      const {
        bleDevice,
        bleServer,
        bleService,
        timerStateCharacteristic,
        timerControlCharacteristic,
      } = await API.connectBle();

      setBleConnected(true);
      bleDevice.addEventListener("gattserverdisconnected", handleBleDisconnect);

      timerStateCharacteristic.addEventListener(
        "characteristicvaluechanged",
        handleTimerStateChange
      );
      timerStateCharacteristic.startNotifications();
      console.log("adding state notifications");

      setBleObj({
        bleDevice,
        bleServer,
        bleService,
        timerStateCharacteristic,
        timerControlCharacteristic,
      });
      setTimeout(() => {
        requestTimerState(timerControlCharacteristic);
      }, 500);
    } catch (error) {
      console.log(error);
      console.log(navigator.userAgent.toLowerCase());
      setBleFailed(true);
      setBleConnected(false);
    }
  };
  const connectBle2 = async () => {
    try {
      // throw new Error('Forced error for testing purposes')
      const {
        bleDevice,
        bleServer,
        bleService,
        timerStateCharacteristic,
        timerControlCharacteristic,
      } = await API.connectBle();

      setBleConnected(true);
      bleDevice.addEventListener("gattserverdisconnected", handleBleDisconnect);

      timerStateCharacteristic.addEventListener(
        "characteristicvaluechanged",
        handleTimerStateChange
      );
      timerStateCharacteristic.startNotifications();
      console.log("adding state notifications");

      setBleObj({
        bleDevice,
        bleServer,
        bleService,
        timerStateCharacteristic,
        timerControlCharacteristic,
      });
      setTimeout(() => {
        requestSettings2(timerControlCharacteristic);
      }, 500);
    } catch (error) {
      console.log(error);
      console.log(navigator.userAgent.toLowerCase());
      setBleFailed(true);
      setBleConnected(false);
    }
  };

  const disconnectBle = async () => {
    await bleObj.bleServer.disconnect();
  };

  return (
    <TimerContext.Provider
      value={{
        webBluetoothAvailable,
        bleConnected,
        disconnectBle,

        serverTimerDisplay,
        serverTimerState,

        timerMode,
        setTimerMode,

        defaultCountdownTime,
        handleSetDefaultCountdown,
        defaultIntervalTime,
        handleSetDefaultInterval,

        countdownTime,
        setCountdownTime,
        intervalTime,
        setIntervalTime,

        connectBle,
        connectBle2,
        handleTimerControls,
        handleNewTimer,

        bleFailed,
        setBleFailed,

        settings,
        requestSettings,
        handleSettingsUpdate,
        handleSettingsReset,
      }}
    >
      {props.children}
    </TimerContext.Provider>
  );
};

export default TimerContextProvider;
