import { React, useContext } from 'react'
import { TimerContext } from '../../context/TimerContext'
import './Controls.css'

const Controls = () => {
  const { timerMode, handleTimerControls, serverTimerState } =
    useContext(TimerContext)

  const handleClick = (button) => {
    handleTimerControls(button)
  }

  const displayButtons = () => {
    switch (serverTimerState[8]) {
      case 0:
        return (
          <div className="Controls">
            <div className="Controls__button " onClick={() => handleClick(2)}>
              RESTART
            </div>
            <div className="Controls__button " onClick={() => handleClick(1)}>
              START
            </div>
          </div>
        )
      case 1:
        return (
          <div className="Controls">
            <div className="Controls__button " onClick={() => handleClick(2)}>
              RESTART
            </div>
            <div className="Controls__button " onClick={() => handleClick(0)}>
              STOP
            </div>
          </div>
        )
      case 2:
        return (
          <div className="Controls">
            <div className="Controls__button " onClick={() => handleClick(2)}>
              RESTART
            </div>
          </div>
        )
    }
  }

  return (
    <div>
      {timerMode === 'current' ? (
        displayButtons()
      ) : (
        <div className="Controls">
          <div className="Controls__button " onClick={() => handleClick(3)}>
            SET TIMER
          </div>
        </div>
      )}
    </div>
  )
}

export default Controls
