const deviceName = 'RollTime'
const bleServiceID = '00000000-0000-0000-0000-000000000001'
const timerStateCharacteristicID = '00000000-0000-0000-0000-000000000002'
const timerControlCharacteristicID = '00000000-0000-0000-0000-000000000003'

const iOSCheck = () => {
  const userAgent = navigator.userAgent.toLowerCase()
  console.log(userAgent)
  const regex = /\(([^)]+)\)/
  const match = userAgent.match(regex)

  if (match[0].indexOf('iphone') !== -1 || match[0].indexOf('ipad') !== -1) {
    console.log('iOS detected')
    if (userAgent.indexOf('bluefy') !== -1) {
      return true
    }
    return false
  }
  return true
}

const webBluetoothCheck = () => {
  if (!navigator.bluetooth) {
    console.log('Web Bluetooth API is not available in this browser!')
    return false
  }
  console.log('Web Bluetooth API supported in this browser.')
  return true
}

const connectBle = async () => {
  const bleDevice = await navigator.bluetooth.requestDevice({
    filters: [{ name: deviceName }],
    optionalServices: [bleServiceID],
  })

  const bleServer = await bleDevice.gatt.connect()
  const bleService = await bleServer.getPrimaryService(bleServiceID)
  const timerStateCharacteristic = await bleService.getCharacteristic(
    timerStateCharacteristicID
  )
  const timerControlCharacteristic = await bleService.getCharacteristic(
    timerControlCharacteristicID
  )

  return {
    bleDevice,
    bleServer,
    bleService,
    timerStateCharacteristic,
    timerControlCharacteristic,
  }
}

