import { React, useContext, useState } from 'react'
import { TimerContext } from '../../context/TimerContext'
import { ReactComponent as BLESvg } from '../../bluetooth-icon.svg'

import './BleScreen.css'

const BleScreen = () => {
  const { webBluetoothAvailable, connectBle } = useContext(TimerContext)

  return (
    <div>
      {webBluetoothAvailable ? (
        <div className="BLE">
          <div className="BLE__connectButton" onClick={connectBle}>
            <BLESvg className="BLE__icon" />
          </div>
          <div className="BLE__title">RollTime</div>
        </div>
      ) : (
        <div className="BLE">
          <div className="BLE__logo" />
          <p className="BLE__message">
            The Web Bluetooth API is not available on your browser. On iOS
            please use{' '}
            <a
              className="BLE__link"
              href="https://apps.apple.com/us/app/bluefy-web-ble-browser/id1492822055"
            >
              Bluefy
            </a>
            . On all other platforms use{' '}
            <a className="BLE__link" href="https://www.google.com/chrome">
              Chrome
            </a>
            .
          </p>
          <div className="BLE__title">RollTime</div>
        </div>
      )}
    </div>
  )
}

export default BleScreen
