import { React } from 'react'
import { formatRoundsNumber } from '../../utils/utils'
import './RoundsSelectionDisplay.css'

const RoundsSelectionDisplay = ({ rounds, onClick }) => {
  const getClassName = () => {
    let name = 'RoundsSelectionDisplay '
    if (onClick === undefined) {
      name += 'RoundsSelectionDisplay__inactive'
    }
    return name
  }
  return (
    <div className={getClassName()} onClick={onClick}>
      {formatRoundsNumber(rounds)}
    </div>
  )
}
export default RoundsSelectionDisplay
