import { React } from 'react'
import { formatTime } from '../../utils/utils'
import './TimeSelectionDisplay.css'

const TimeSelectionDisplay = ({ time, onClick }) => {
  const getClassName = () => {
    let name = 'TimeSelectionDisplay '
    if (onClick === undefined) {
      name += 'TimeSelectionDisplay__inactive'
    }
    return name
  }
  return (
    <div className={getClassName()} onClick={onClick}>
      {formatTime(time)}
    </div>
  )
}
export default TimeSelectionDisplay
