import { React, useState, useContext } from 'react'
import { TimerContext } from '../../context/TimerContext'
import './TabBar.css'

const TabBar = () => {
  const { timerMode, setTimerMode } = useContext(TimerContext)
  const [isFlashing, setFlashing] = useState(false)

  const handleTabClick = (index) => {
    setTimerMode(index)
    setFlashing(true)
    setTimeout(() => {
      setFlashing(false)
    }, 2000)
  }

  return (
    <div className="TabBar">
      <div className="TabBar__tab">
        <div
          className={`TabBar__button ${
            timerMode === 'timer' ? 'TabBar__button__active' : ''
          }`}
          style={{
            animation:
              isFlashing && timerMode === 'timer' ? 'flash1 500ms linear' : '',
          }}
          onClick={() => handleTabClick('timer')}
        >
          Timer
          <div
            className="TabBar__flashHelper"
            style={{
              animation:
                isFlashing && timerMode === 'timer'
                  ? 'flash2 500ms linear'
                  : '',
            }}
          />
        </div>
      </div>
      <div className="TabBar__tab">
        <div
          className={`TabBar__button ${
            timerMode === 'countdown' ? 'TabBar__button__active' : ''
          }`}
          style={{
            animation:
              isFlashing && timerMode === 'countdown'
                ? 'flash1 500ms linear'
                : '',
          }}
          onClick={() => handleTabClick('countdown')}
        >
          Countdown
          <div
            className="TabBar__flashHelper"
            style={{
              animation:
                isFlashing && timerMode === 'countdown'
                  ? 'flash2 1000ms linear'
                  : '',
            }}
          />
        </div>
      </div>
      <div className="TabBar__tab">
        <div
          className={`TabBar__button ${
            timerMode === 'interval' ? 'TabBar__button__active' : ''
          }`}
          style={{
            animation:
              isFlashing && timerMode === 'interval'
                ? 'flash1 1000ms linear'
                : '',
          }}
          onClick={() => handleTabClick('interval')}
        >
          Interval
          <div
            className="TabBar__flashHelper"
            style={{
              animation:
                isFlashing && timerMode === 'interval'
                  ? 'flash2 1000ms linear'
                  : '',
            }}
          />
        </div>
      </div>
      <div className="TabBar__tab">
        <div
          className={`TabBar__button ${
            timerMode === 'current' ? 'TabBar__button__active' : ''
          }`}
          style={{
            animation:
              isFlashing && timerMode === 'current'
                ? 'flash1 1000ms linear'
                : '',
          }}
          onClick={() => handleTabClick('current')}
        >
          Current
          <div
            className="TabBar__flashHelper"
            style={{
              animation:
                isFlashing && timerMode === 'current'
                  ? 'flash2 1000ms linear'
                  : '',
            }}
          />
        </div>
      </div>
    </div>
  )
}

export default TabBar
