import { React, forwardRef } from 'react'
import Picker from 'react-mobile-picker'
import { formatTimeNumber } from '../../utils/utils'
import './TimeWheel.css'

const timeList1 = Array.from(Array(60).keys()).map((num) =>
  formatTimeNumber(num)
)
const timeList2 = Array.from(Array(100).keys()).map((num) =>
  formatTimeNumber(num)
)

const selections = {
  hours: timeList2,
  minutes: timeList1,
  seconds: timeList1,
}

const TimeWheel = forwardRef(({ selectedTime, setSelectedTime }, ref) => {
  const pickerValue = {
    hours: selectedTime[0],
    minutes: selectedTime[1],
    seconds: selectedTime[2],
  }

  const handlePickerChange = (newValue, key) => {
    setSelectedTime([newValue.hours, newValue.minutes, newValue.seconds])
  }

  return (
    <div className="TimeWheel" ref={ref}>
      <div className="TimeWheel__header">
        <div>Hours</div>
        <div>Minutes</div>
        <div>Seconds</div>
      </div>

      <Picker
        value={pickerValue}
        onChange={handlePickerChange}
        wheelMode="normal"
      >
        {Object.keys(selections).map((name) => (
          <Picker.Column key={name} name={name}>
            {selections[name].map((option) => (
              <Picker.Item key={option} value={option}>
                {option}
              </Picker.Item>
            ))}
          </Picker.Column>
        ))}
      </Picker>
    </div>
  )
})
export default TimeWheel
