import { React, useContext } from 'react'
import DialogModal2 from '../DialogModal2'
import BleScreen from '../BleScreen/BleScreen'
import { TimerContext } from '../../context/TimerContext'

const BleModal = () => {
  const { bleFailed, setBleFailed } = useContext(TimerContext)

  const handleOk = () => {
    setBleFailed(false)
  }

  return (
    <div>
      {bleFailed ? (
        <div className="CountdownModal TimeModal">
          <DialogModal2 isOpen={bleFailed} handleOk={handleOk}>
            Bluetooth Connection Failed. Please ensure you are using Chrome on
            Android or Bluefy on iOS.
          </DialogModal2>
        </div>
      ) : (
        <BleScreen />
      )}
    </div>
  )
}

export default BleModal
