import { React, useContext } from 'react'
import TimeDisplay from '../TimeSelectionDisplay'
import { TimerContext } from '../../context/TimerContext'
import './TimerModal.css'

const TimerModal = () => {
  const { timerMode } = useContext(TimerContext)

  return (
    <div>
      {timerMode == 'timer' ? (
        <div className="TimerModal TimeModal">
          <TimeDisplay time={[0, 0, 0]} />
        </div>
      ) : (
        <></>
      )}
    </div>
  )
}

export default TimerModal
