import { React, useContext } from 'react'
import { TimerContext, timerModeMap } from '../../context/TimerContext'
import { formatTimeNumber, formatTime } from '../../utils/utils'
import './TimeDisplay.css'

const TimeDisplay = () => {
  const { serverTimerDisplay, serverTimerState } = useContext(TimerContext)

  return (
    <div
      className={`TimeDisplay ${
        serverTimerState[8] != 1 ? 'TimeDisplay__inactive' : ''
      }`}
    >
      <div className="TimeDisplay__time">
        {formatTime(serverTimerDisplay.slice(1, 4))}{' '}
      </div>
      <div className="TimeDisplay__mode">
        {serverTimerDisplay[5] > 0
          ? timerModeMap[serverTimerDisplay[0]] +
            ': ' +
            formatTimeNumber(serverTimerDisplay[5])
          : timerModeMap[serverTimerDisplay[0]]}
      </div>
    </div>
  )
}
export default TimeDisplay
