export const formatTimeNumber = (num) => {
  return num.toString().padStart(2, '0')
}
export const formatRoundsNumber = (num) => {
  if (num == 0 || num == '∞') {
    return '∞'
  }
  return num.toString().padStart(2, '0')
}

export const formatTime = (time) => {
  const timeParts = []
  timeParts.push(formatTimeNumber(time[0]))
  timeParts.push(formatTimeNumber(time[1]))
  timeParts.push(formatTimeNumber(time[2]))

  return timeParts.join(':')
}
