import { React } from 'react'
import { Dialog } from '@headlessui/react'
import { formatTime, formatRoundsNumber } from '../../utils/utils'

import './DialogModal.css'

const DialogModal = ({
  isOpen,
  handleCancel,
  handleSaveSelected,
  defaultTime,
  handleUseDefault,
  handleSetDefault,
  isRound,
  children,
}) => {
  const getDefaultTitle = () => {
    let title = 'default: '
    if (isRound) {
      title += formatRoundsNumber(defaultTime)
    } else {
      title += formatTime(defaultTime)
    }
    return title
  }
  return (
    <div className="DialogModal">
      <Dialog
        as="div"
        open={isOpen}
        onClose={handleCancel}
        className="DialogModal__container"
      >
        <div />
        <div className="DialogModal__overlay" aria-hidden="true" />
        <div className="DialogModal__dialogContainer">
          <div className="DialogModal__dialogContent">
            <Dialog.Panel className="DialogModal__dialog">
              {children}
              <div className="DialogModal__defaultTime">
                {getDefaultTitle()}
              </div>
              <div className="DialogModal__buttonContainer">
                <div className="DialogModal__buttonSubContainer1">
                  <div
                    className="DialogModal__button1"
                    onClick={handleUseDefault}
                  >
                    use default
                  </div>
                  <div
                    className="DialogModal__button1"
                    onClick={handleSetDefault}
                  >
                    save as default
                  </div>
                </div>
                <div className="DialogModal__buttonSubContainer2">
                  <div
                    onClick={handleCancel}
                    className="DialogModal__button2 DialogModal__cancel"
                  >
                    CANCEL
                  </div>
                  <div
                    onClick={handleSaveSelected}
                    className="DialogModal__button2 DialogModal__save"
                  >
                    SAVE
                  </div>
                </div>
              </div>
            </Dialog.Panel>
          </div>
        </div>
      </Dialog>
    </div>
  )
}
export default DialogModal
