import { React, useContext } from "react";
import BleModal from "../BleModal/BleModal";
import BleScreen from "../BleScreen/BleScreen";
import TimeDisplay from "../TimeDisplay/TimeDisplay";
import TabBar from "../TabBar";
import TimerModal from "../TimerModal";
import CountdownModal from "../CountdownModal";
import IntervalModal from "../IntervalModal";
import CurrentModal from "../CurrentModal";
import Controls from "../Controls";
import BleDisconnectScreen from "../BleDisconnectScreen";
import { TimerContext } from "../../context/TimerContext";
import "./MainScreen.css";

const MainScreen = () => {
  const { bleConnected } = useContext(TimerContext);

  return (
    <div className="Main">
      {bleConnected ? (
        <div>
          <TimeDisplay />
          <TabBar />
          <TimerModal />
          <CountdownModal />
          <IntervalModal />
          <CurrentModal />
          <Controls />
          <BleDisconnectScreen />
        </div>
      ) : (
        <div>
          {/* <BleScreen /> */}
          <BleModal />
        </div>
      )}
    </div>
  );
};

export default MainScreen;
