import { React, useState, useContext } from 'react'
import TimeWheel from '../../components/TimeWheel'
import TimeSelectionDisplay from '../TimeSelectionDisplay'
import DialogModal from '../DialogModal'
import { TimerContext } from '../../context/TimerContext'
import { formatTimeNumber } from '../../utils/utils'
import './CountdownModal.css'

const CountdownModal = () => {
  const {
    timerMode,
    defaultCountdownTime,
    handleSetDefaultCountdown,
    countdownTime,
    setCountdownTime,
  } = useContext(TimerContext)

  const [isOpen, setIsOpen] = useState(false)
  const [selectedTime, setSelectedTime] = useState([
    formatTimeNumber(countdownTime[0]),
    formatTimeNumber(countdownTime[1]),
    formatTimeNumber(countdownTime[2]),
  ])

  const handleSaveSelected = () => {
    setIsOpen(false)
    setCountdownTime(selectedTime)
  }

  const handleCancel = () => {
    setIsOpen(false)
    setSelectedTime([
      formatTimeNumber(countdownTime[0]),
      formatTimeNumber(countdownTime[1]),
      formatTimeNumber(countdownTime[2]),
    ])
  }

  const handleUseDefault = () => {
    const newTime = [
      formatTimeNumber(defaultCountdownTime[0]),
      formatTimeNumber(defaultCountdownTime[1]),
      formatTimeNumber(defaultCountdownTime[2]),
    ]
    setSelectedTime(newTime)
    setCountdownTime(newTime)
    setIsOpen(false)
  }

  const handleSetDefault = () => {
    handleSetDefaultCountdown(selectedTime)
  }

  return (
    <div>
      {timerMode == 'countdown' ? (
        <div className="CountdownModal TimeModal">
          <TimeSelectionDisplay
            time={countdownTime}
            onClick={() => setIsOpen(true)}
          />

          <DialogModal
            isOpen={isOpen}
            handleCancel={handleCancel}
            handleSaveSelected={handleSaveSelected}
            defaultTime={defaultCountdownTime}
            handleSetDefault={handleSetDefault}
            handleUseDefault={handleUseDefault}
          >
            <TimeWheel
              selectedTime={selectedTime}
              setSelectedTime={setSelectedTime}
            />
          </DialogModal>
        </div>
      ) : (
        <></>
      )}
    </div>
  )
}

export default CountdownModal
