import { React, useContext } from 'react'
import TimeSelectionDisplay from '../TimeSelectionDisplay'
import RoundsSelectionDisplay from '../RoundsSelectionDisplay'
import { TimerContext } from '../../context/TimerContext'
import './CurrentModal.css'

const CurrentModal = () => {
  const { timerMode, serverTimerState } = useContext(TimerContext)

  const getDisplay = () => {
    let display
    if (serverTimerState[0] == 0) {
      display = (
        <div className="CurrentModal TimeModal">
          <div className="CurrentModal__title">Timer</div>
          <TimeSelectionDisplay time={[0, 0, 0]} />
        </div>
      )
    } else if (serverTimerState[0] == 1) {
      display = (
        <div className="CurrentModal TimeModal">
          <div className="CurrentModal__title">Countdown Timer</div>
          <TimeSelectionDisplay time={serverTimerState.slice(1, 4)} />
        </div>
      )
    } else if (serverTimerState[0] == 2) {
      display = (
        <div className="CurrentModal TimeModal">
          <div className="CurrentModal__title">Interval Timer</div>
          <div className="CurrentModal__timeSet">
            <div className="CurrentModal__timeLabel">work</div>
            <TimeSelectionDisplay time={serverTimerState.slice(1, 4)} />
          </div>
          <div className="CurrentModal__timeSet">
            <div className="CurrentModal__timeLabel">rest</div>
            <TimeSelectionDisplay time={serverTimerState.slice(4, 7)} />
          </div>
          <div className="IntervalModal__timeSet">
            <div className="IntervalModal__timeLabel">rounds</div>
            <RoundsSelectionDisplay rounds={serverTimerState[7]} />
          </div>
        </div>
      )
    }

    return display
  }

  return <div>{timerMode == 'current' ? getDisplay() : <></>}</div>
}

export default CurrentModal
