import { React, forwardRef } from 'react'
import Picker from 'react-mobile-picker'
import { formatRoundsNumber } from '../../utils/utils'
import './RoundsWheel.css'

const roundsList = Array.from(Array(100).keys()).map((num) =>
  formatRoundsNumber(num)
)

const selections = {
  rounds: roundsList,
}

const RoundsWheel = forwardRef(({ selectedRounds, setSelectedRounds }, ref) => {
  const pickerValue = {
    rounds: selectedRounds,
  }

  const handlePickerChange = (newValue, key) => {
    setSelectedRounds(newValue.rounds)
  }

  return (
    <div className="RoundsWheel" ref={ref}>
      <div className="RoundsWheel__header">
        <div>Rounds</div>
      </div>

      <Picker
        value={pickerValue}
        onChange={handlePickerChange}
        wheelMode="normal"
      >
        {Object.keys(selections).map((name) => (
          <Picker.Column key={name} name={name}>
            {selections[name].map((option) => (
              <Picker.Item key={option} value={option}>
                {option}
              </Picker.Item>
            ))}
          </Picker.Column>
        ))}
      </Picker>
    </div>
  )
})
export default RoundsWheel
