import { React, useEffect, useContext, useState } from "react";
import { TimerContext } from "../../context/TimerContext";
import "./SettingsScreen.css";

const SettingsScreen = () => {
  const {
    settings,
    requestSettings,
    handleSettingsUpdate,
    handleSettingsReset,
    bleConnected,
    connectBle2,
    disconnectBle,
  } = useContext(TimerContext);
  const [editedSettings, setEditedSettings] = useState("");

  useEffect(() => {
    requestSettings();
  }, []);

  useEffect(() => {
    setEditedSettings(settings);
  }, [settings]);

  const handleSave = () => {
    handleSettingsUpdate(editedSettings);
  };

  return (
    <div className="SettingsScreen">
      <h1>Settings</h1>

      {/* BLE Connection Button */}
      <button
        className="BleConnectButton"
        onClick={bleConnected ? disconnectBle : connectBle2}
      >
        {bleConnected ? "Disconnect Bluetooth" : "Connect Bluetooth"}
      </button>

      <textarea
        className="SettingsTextArea"
        value={editedSettings}
        onChange={(e) => setEditedSettings(e.target.value)}
      />
      <div>
        <button className="SettingsSaveButton" onClick={handleSettingsReset}>
          Reset
        </button>
        <button className="SettingsSaveButton" onClick={handleSave}>
          Save
        </button>
      </div>
    </div>
  );
};

export default SettingsScreen;
