import { React, useState, useContext } from 'react'
import TimeDisplay from '../TimeSelectionDisplay'
import RoundsSelectionDisplay from '../RoundsSelectionDisplay'
import TimeWheel from '../../components/TimeWheel'
import RoundsWheel from '../RoundsWheel/RoundsWheel'
import TransitionModal from '../DialogModal'
import { TimerContext } from '../../context/TimerContext'
import { formatTimeNumber, formatRoundsNumber } from '../../utils/utils'
import './IntervalModal.css'

const IntervalModal = () => {
  const {
    timerMode,
    defaultIntervalTime,
    handleSetDefaultInterval,
    intervalTime,
    setIntervalTime,
  } = useContext(TimerContext)

  const [isOpen1, setIsOpen1] = useState(false)
  const [isOpen2, setIsOpen2] = useState(false)
  const [isOpen3, setIsOpen3] = useState(false)
  const [selectedTime1, setSelectedTime1] = useState([
    formatTimeNumber(intervalTime[0]),
    formatTimeNumber(intervalTime[1]),
    formatTimeNumber(intervalTime[2]),
  ])
  const [selectedTime2, setSelectedTime2] = useState([
    formatTimeNumber(intervalTime[3]),
    formatTimeNumber(intervalTime[4]),
    formatTimeNumber(intervalTime[5]),
  ])
  const [selectedRounds, setSelectedRounds] = useState(
    formatRoundsNumber(intervalTime[6])
  )

  const handleSaveSelected1 = () => {
    setIsOpen1(false)
    const newTime = [
      selectedTime1[0],
      selectedTime1[1],
      selectedTime1[2],
      intervalTime[3],
      intervalTime[4],
      intervalTime[5],
      intervalTime[6],
    ]
    setIntervalTime(newTime)
  }

  const handleSaveSelected2 = () => {
    setIsOpen2(false)
    const newTime = [
      intervalTime[0],
      intervalTime[1],
      intervalTime[2],
      selectedTime2[0],
      selectedTime2[1],
      selectedTime2[2],
      intervalTime[6],
    ]
    setIntervalTime(newTime)
  }

  const handleSaveSelected3 = () => {
    setIsOpen3(false)
    const newTime = [
      intervalTime[0],
      intervalTime[1],
      intervalTime[2],
      intervalTime[3],
      intervalTime[4],
      intervalTime[5],
      selectedRounds,
    ]
    setIntervalTime(newTime)
  }

  const handleCancel1 = () => {
    setIsOpen1(false)
    setSelectedTime1([
      formatTimeNumber(intervalTime[0]),
      formatTimeNumber(intervalTime[1]),
      formatTimeNumber(intervalTime[2]),
    ])
  }
  const handleCancel2 = () => {
    setIsOpen2(false)
    setSelectedTime2([
      formatTimeNumber(intervalTime[3]),
      formatTimeNumber(intervalTime[4]),
      formatTimeNumber(intervalTime[5]),
    ])
  }
  const handleCancel3 = () => {
    setIsOpen3(false)
    setSelectedTime2(formatRoundsNumber(intervalTime[6]))
  }

  const handleUseDefault1 = () => {
    const newTime = [
      formatTimeNumber(defaultIntervalTime[0]),
      formatTimeNumber(defaultIntervalTime[1]),
      formatTimeNumber(defaultIntervalTime[2]),
      formatTimeNumber(intervalTime[3]),
      formatTimeNumber(intervalTime[4]),
      formatTimeNumber(intervalTime[5]),
      formatRoundsNumber(intervalTime[6]),
    ]
    setSelectedTime1(newTime.slice(0, 3))
    setIntervalTime(newTime)
    setIsOpen1(false)
  }
  const handleUseDefault2 = () => {
    const newTime = [
      formatTimeNumber(intervalTime[0]),
      formatTimeNumber(intervalTime[1]),
      formatTimeNumber(intervalTime[2]),
      formatTimeNumber(defaultIntervalTime[3]),
      formatTimeNumber(defaultIntervalTime[4]),
      formatTimeNumber(defaultIntervalTime[5]),
      formatRoundsNumber(intervalTime[6]),
    ]
    setSelectedTime2(newTime.slice(3, 6))
    setIntervalTime(newTime)
    setIsOpen2(false)
  }

  const handleUseDefault3 = () => {
    const newTime = [
      formatTimeNumber(intervalTime[0]),
      formatTimeNumber(intervalTime[1]),
      formatTimeNumber(intervalTime[2]),
      formatTimeNumber(intervalTime[3]),
      formatTimeNumber(intervalTime[4]),
      formatTimeNumber(intervalTime[5]),
      formatRoundsNumber(defaultIntervalTime[6]),
    ]
    setSelectedRounds(newTime[0])
    setIntervalTime(newTime)
    setIsOpen3(false)
  }

  const handleSetDefault1 = () => {
    const defaultTime = [...selectedTime1, ...defaultIntervalTime.slice(3, 7)]
    handleSetDefaultInterval(defaultTime)
  }

  const handleSetDefault2 = () => {
    const defaultTime = [
      ...defaultIntervalTime.slice(0, 3),
      ...selectedTime2,
      defaultIntervalTime[6],
    ]
    handleSetDefaultInterval(defaultTime)
  }

  const handleSetDefault3 = () => {
    const defaultTime = [...defaultIntervalTime.slice(0, 6), selectedRounds]
    console.log(defaultTime)
    handleSetDefaultInterval(defaultTime)
  }

  return (
    <div>
      {timerMode == 'interval' ? (
        <div className="IntervalModal TimeModal">
          <div className="IntervalModal__timeSet">
            <div className="IntervalModal__timeLabel">work</div>
            <TimeDisplay
              time={intervalTime.slice(0, 3)}
              onClick={() => setIsOpen1(true)}
            />
          </div>
          <div className="IntervalModal__timeSet">
            <div className="IntervalModal__timeLabel">rest</div>
            <TimeDisplay
              time={intervalTime.slice(3, 6)}
              onClick={() => setIsOpen2(true)}
            />
          </div>
          <div className="IntervalModal__timeSet">
            <div className="IntervalModal__timeLabel">rounds</div>
            <RoundsSelectionDisplay
              rounds={intervalTime[6]}
              onClick={() => setIsOpen3(true)}
            />
          </div>

          <TransitionModal
            isOpen={isOpen1}
            handleCancel={handleCancel1}
            handleSaveSelected={handleSaveSelected1}
            defaultTime={defaultIntervalTime.slice(0, 3)}
            handleSetDefault={handleSetDefault1}
            handleUseDefault={handleUseDefault1}
          >
            <TimeWheel
              selectedTime={selectedTime1}
              setSelectedTime={setSelectedTime1}
            />
          </TransitionModal>

          <TransitionModal
            isOpen={isOpen2}
            handleCancel={handleCancel2}
            handleSaveSelected={handleSaveSelected2}
            defaultTime={defaultIntervalTime.slice(3, 6)}
            handleSetDefault={handleSetDefault2}
            handleUseDefault={handleUseDefault2}
          >
            <TimeWheel
              selectedTime={selectedTime2}
              setSelectedTime={setSelectedTime2}
            />
          </TransitionModal>
          <TransitionModal
            isOpen={isOpen3}
            handleCancel={handleCancel3}
            handleSaveSelected={handleSaveSelected3}
            defaultTime={defaultIntervalTime[6]}
            handleSetDefault={handleSetDefault3}
            handleUseDefault={handleUseDefault3}
            isRound={true}
          >
            <RoundsWheel
              rounds={intervalTime[6]}
              selectedRounds={selectedRounds}
              setSelectedRounds={setSelectedRounds}
            />
          </TransitionModal>
        </div>
      ) : (
        <></>
      )}
    </div>
  )
}

export default IntervalModal
